.projects-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 70px;
}

.projects-container h1 {
  font-family: "IBM Plex Mono";
  font-size: 32px;
  color: white;
}

.projects-container .ant-card-meta-title,
.ant-card-meta-description {
  font-family: "Monda";
}

.projects-container .description-container {
  margin-bottom: 10px;
}

.projects-container .ant-tag {
  margin-bottom: 5px;
}
